import React, {useCallback, useState} from 'react';
import styles from './style.module.scss';
import {useDispatch} from 'react-redux';
import {setWeatherTC} from '../../redux/wether';
import {Container} from '../../helper/wrapper';
import {TextField} from '@mui/material';
import combineCss from "../../helper/combine-css";

const SearchPanel = ({setShouldInfo, shouldInfo}) => {
    const dispatch = useDispatch();
    const [value, setValue] = useState('');


    const showInfo = useCallback(
        (e) => {
            if (e.key === 'Enter') {
                dispatch(setWeatherTC(value));
                setValue('');
                setShouldInfo(true);
            }
        },
        [setShouldInfo, value, dispatch]
    );

    return (
        <div className={combineCss(styles.wrapper, shouldInfo && styles.hidden)}>
            <Container>
                <div className={styles.searchBox}>
                    <h1 className={styles.logo}>Weather</h1>
                    <TextField
                        id='standard-basic'
                        label='Search...'
                        variant='standard'
                        className={styles.root}
                        color='primary'
                        type='text'
                        onChange={(e) => {
                            setValue(e.currentTarget.value);
                        }}
                        value={value}
                        onKeyPress={showInfo}
                    />
                </div>
            </Container>
        </div>
    );
};

export default SearchPanel;
